import { MenuBarExtra } from "@raycast/api";
import moment from "moment-timezone";

export default function Command() {
  const timezones = [
    {
      name: "San Francisco",
      tz: "US/Pacific",
    },
    {
      name: "New York",
      tz: "US/Eastern",
    },
  ];
  const now = moment();
  const times = timezones.map((tz) => ({
    name: tz.name,
    time: now.tz(tz.tz).format("HH:mm A"),
  }));

  return <MenuBarExtra title={times.map((t) => t.name + ": " + t.time).join("  |  ")} />;
}
